li {
  margin-bottom: 10px;
}

#linkcool {
  display: inline-block;
  color: #5f3263;
  text-decoration: none;
}

#linkcool::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #5f3263;
  transition: width 0.3s;
}

#linkcool:hover::after {
  width: 100%;
  transition: width 0.3s;
}

#btnLogin {
  color: #5f3263;
  border-color: #5f3263;
}
#btnLogin:hover {
  background-color: #5f3263;
  border-color: #5f3263;
  color: white;
}

#btnSingup {
  background-color: #f37b8c;
  color: #ffffff;
  border-color: #5f3263;
}
#btnSingup:hover {
  background-color: #eb5468;
}

h1 {
  color: #3d3c3c;
  font-size: 50px;
  font-weight: bold;
  font-family: monospace;
  letter-spacing: 7px;
  cursor: pointer;
}
h1 span {
  transition: 0.5s linear;
}
h1:hover span:nth-child(1) {
  margin-right: 4px;
}
h1:hover span:nth-child(1):after {
  content: "'";
}
h1:hover span:nth-child(2) {
  margin-left: 5px;
}
h1:hover span {
  color: #ffffff;
  text-shadow: 0 0 10px #000000, 0 0 20px #000000, 0 0 40px #000000;
}
